import { combineReducers } from "redux";

import { SET_FILTER, SET_MOVIES, SET_USER } from "../actions/actions";

// visibilityFilter is a function (aka reducer)
//Each reducer takes a state and an action, and if its concerned by the action, it changes the state and is only cares about what its responsible for.
function visibilityFilter(state = "", action) {
  switch (action.type) {
    case SET_FILTER:
      return action.value;
    default:
      return state;
  }
}

// The line in the code above:
// visibilityFilter(state = '', action)
// is an identity card - Every time an action is dispatched, this reducer will be called, and its responsible for addressing the action or not, hence the switch-case syntax

function movies(state = [], action) {
  switch (action.type) {
    case SET_MOVIES:
      return action.value;
    default:
      return state;
  }
}

function loggedInUser(state = [], action) {
  switch (action.type) {
    case SET_USER:
      return action.value;
    default:
      return state;
  }
}

const moviesApp = combineReducers({
  visibilityFilter,
  movies,
  loggedInUser
});

export default moviesApp;
